import { Button, ButtonGroup, TextField } from '@mui/material';
import { oneItemStore } from '../store';

const File = () => {
  const {
    setItemField,
    getItemFields,
    sendItem,
  } = oneItemStore(({ setItemField, getItemFields, sendItem }) =>
    ({ setItemField, getItemFields, sendItem }));

  const { name, price } = getItemFields();

  return (
    <>
      <div style={{ display: 'flex', justifyContent: 'space-between' }} className="add_purchase_inputs">
        <TextField
          sx={{ width: '35%' }}
          id="outlined-basic"
          label="Title"
          variant="outlined"
          value={name}
          onChange={(e) => setItemField('name', e.target.value)}
        />
        <TextField
          sx={{ width: '25%' }}
          id="outlined-basic"
          label="Price"
          variant="outlined"
          value={price}
          onChange={(e) => setItemField('price', e.target.value)}
        />
      </div>
      <ButtonGroup style={{ display: 'flex', marginTop: '50px' }} variant="outlined" aria-label="large button group">
        <Button onClick={() => {
          sendItem();
        }}
        >
          Add purchase
        </Button>
      </ButtonGroup>
    </>
  );
};

export default File;
